import React from 'react';
import Square from './Square';


class Board extends React.Component {
    renderSquare(i) {
      return (
        <Square
          value={this.props.squares[i]}
          id={i}
          onClick={() => this.props.onClick(i)}
          winVal={this.props.winVal}
        />
      );
    }
  
    render() {
      var index1 = Array.from({length: 3}, (elm, index) => {
        return index;
      }); 
      return (
        <div>
          {index1.map((e,i) => {
            return(
              <div key={i} className="board-row">
                {this.renderSquare(3*e)}
                {this.renderSquare(3*e+1)}
                {this.renderSquare(3*e+2)}
              </div>
            )    
          })}
        </div>
      );
    }
}

export default Board;