import React from 'react';
import Board from './Board';
  
  class App extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        history: [
          {
            squares: Array(9).fill(null),
            currentSquare:Array(2).fill(null),
          }
        ],
        stepNumber: 0,
        xIsNext: true
      };
    }
  
    handleClick(i) {
      const history = this.state.history.slice(0, this.state.stepNumber + 1);
      const current = history[history.length - 1];
      const squares = current.squares.slice();
      const currentSquare=current.currentSquare.slice();
      if (calculateWinner(squares) || squares[i]) {
        return;
      }
      squares[i] = this.state.xIsNext ? "X" : "O";
      
      const col=(i%3)+1;
      const row=i%3?((i-col+1)/3)+1:(i/3)+1;
      currentSquare[0]=row;
      currentSquare[1]=col;

      this.setState({
        history: history.concat([
          {
            squares: squares,
            currentSquare: currentSquare,
          }
        ]),
        stepNumber: history.length,
        xIsNext: !this.state.xIsNext
      });
    }
  
    jumpTo(move) {
      this.setState({
        stepNumber: move,
        xIsNext: (move % 2) === 0
      });
    }
  
    render() {
      const history = this.state.history;
      const current = history[this.state.stepNumber];
      const winner = calculateWinner(current.squares);
      const moves = history.map((step, move) => {
        const desc = move ?
          'Go to move #' + move + ' ('+ step.currentSquare + ')':
          'Go to game start';
        return (
          <li className="list-group-item list-group-item-info" key={move}>
            <button className="btn" onClick={() => this.jumpTo(move)}>{desc}</button>
          </li>
        );
      });
  
      let status;
      if (winner) {
        status = `Winner: ${winner[0]}`;
      } else if (this.state.stepNumber === 9) {
        status = "It's a Draw!";
      } else {
        status = "Next player: " + (this.state.xIsNext ? "X" : "O");
      }
  
      return (
        <div className="container p-4 mb-5 row game">
          <div className="col-sm-5 pt-4">
            <Board
              squares={current.squares}
              onClick={i => this.handleClick(i)}
              winVal={winner}
            />
          </div>
          <div className="col-sm-7 p-5 mt-3 game-info">
            <div className="h4 my-3 text-center g-in">{status}</div>
            <ol className="list-group list-group-numbered">{moves}</ol>
          </div>
        </div>
      );
    }
  }
  
  // ========================================
  
  function calculateWinner(squares) {
    const lines = [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],
      [0, 3, 6],
      [1, 4, 7],
      [2, 5, 8],
      [0, 4, 8],
      [2, 4, 6]
    ];
    for (let i = 0; i < lines.length; i++) {
      const [a, b, c] = lines[i];
      if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
        return [squares[a],a,b,c];
      }
    }
    return null;
  }

  export default App;